    width: 100%; } }

@media (min-width: 480px) {
  .lista-invitados {
    grid-template-columns: 1fr 1fr 1fr; }
  .lista-invitados li {
    width: 100%; } }

.invitado {
  position: relative;
  overflow: hidden;
  /*S√≠ algo se sale lo oculta*/ }

.invitado img {
  max-width: 100%;
  filter: grayscale(1);
  /* Pone la imagen en blanco y negro */
  -webkit-filter: grayscale(1);
  /*Prefijo para chrome*/
  transition: all .5s ease-in-out; }

.invitado img:hover {
  max-width: 100%;
  filter: grayscale(0);
  /*Pone la imagen en color*/
  -webkit-filter: grayscale(0);
  /*Pone la imagen en color*/
  transform: scale(1.1); }

.invitado p {
  position: absolute;
  bottom: 0;
